<template>
	<div id="tabbar">
		<van-tabbar v-model="active">
			<van-tabbar-item icon="shop" :to="{ name : 'Index'}">
				<span class="tabText">首页</span>
				<template slot="icon" slot-scope="props">
		      <img :src="props.active ? icons[0].active : icons[0].normal" />
		    </template>
			</van-tabbar-item>
			<van-tabbar-item icon="chat" :to="{ name : 'ChooseGift'}">
				<span class="tabText">选礼</span>
				<template slot="icon" slot-scope="props">
		      <img :src="props.active ? icons[1].active : icons[1].normal" />
		    </template>
			</van-tabbar-item>
			<van-tabbar-item icon="records" :to="{ name : 'SendGift'}">
				<span class="tabText">送礼</span>
				<template slot="icon" slot-scope="props">
		      <img :src="props.active ? icons[2].active : icons[2].normal" />
		    </template>
			</van-tabbar-item>
			<van-tabbar-item icon="gold-coin" :to="{ name : 'Mine'}">
				<span class="tabText">我的</span>
				<template slot="icon" slot-scope="props">
		      <img :src="props.active ? icons[3].active : icons[3].normal" />
		    </template>
			</van-tabbar-item>
		</van-tabbar>
	</div>
</template>

<script>
	export default { 
		name: 'tabbar',
		data() {
			return {
				active: 0,
				icons: [
				{
	        normal: require('../../assets/index/common_btn_home_nor@2x.png'),
	        active: require('../../assets/index/common_btn_home_pre@2x.png')
	     	},
				{
	        normal: require('../../assets/index/common_btn_present_nor@2x.png'),
	        active: require('../../assets/index/common_btn_present_pre@2x.png')
	     	},
				{
	        normal: require('../../assets/index/common_btn_present_nor@2x.png'),
	        active: require('../../assets/index/common_btn_present_pre@2x.png')
	     	},
				{
	        normal: require('../../assets/index/common_btn_my_nor@2x.png'),
	        active: require('../../assets/index/common_btn_my_pre@2x.png')
	     	},
				]
			}
		},
		mounted(){
			let routerName = this.$route.name
			switch (routerName){
				case 'Index':
					this.active = 0
					break;
				case 'ChooseGift':
					this.active = 1
					break;
				case 'SendGift':
					this.active = 2
					break;
				case 'Mine':
					this.active = 3
					break;
				default:
					break;
			}
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}
	
	ul {
		list-style-type: none;
		padding: 0;
	}
	
	li {
		display: inline-block;
		margin: 0 10px;
	}
	
	a {
		color: #42b983;
	}
	.van-tabbar-item{
		color: #ccc;
	}
	/*.tabText{
		color: #ccc;
	}*/
</style>


